.grid {
    margin: 0 -5px;
    letter-spacing: -0.31em; /* Webkit: collapse white-space between units */
    *letter-spacing: normal; /* reset IE < 8 */
    *word-spacing: -0.43em; /* IE < 8: collapse white-space between units */
    text-rendering: optimizespeed; /* Webkit: fixes text-rendering: optimizeLegibility */

    /* Use flexbox when possible to avoid `letter-spacing` side-effects. */
    display: -webkit-box;
    display: -webkit-flex;
    display: -ms-flexbox;
    display: flex;
    -webkit-flex-flow: row wrap;
        -ms-flex-flow: row wrap;
            flex-flow: row wrap;

    /* Prevents distributing space between rows */
    -webkit-align-content: flex-start;
        -ms-flex-line-pack: start;
            align-content: flex-start;
}

.grid-sidebar { flex-wrap: nowrap; }

/* IE10 display: -ms-flexbox (and display: flex in IE 11) does not work inside a table; fall back to block and rely on font hack */
@media all and (-ms-high-contrast: none), (-ms-high-contrast: active) {
	table .grid {
		display: block;
	}
}


/* Opera as of 12 on Windows needs word-spacing.
   The ".opera-only" selector is used to prevent actual prefocus styling
   and is not required in markup.
*/
.opera-only :-o-prefocus,
.grid {
    word-spacing: -0.43em;
}

/* Gutters */
.grid:not(.no-gutters) > * {
    padding-left: 5px;
    padding-right: 5px;
}

.box-generic>.grid { margin-top: 10px; }

.col {
    display: inline-block;
    *display: inline; /* IE < 8: fake inline-block */
    zoom: 1;
    letter-spacing: normal;
    word-spacing: normal;
    vertical-align: top;
    text-rendering: auto;
}

/*
Resets the font family back to the OS/browser's default sans-serif font,
this the same font stack that Normalize.css sets for the `body`.
*/
.grid [class *= "pure-u"] {
    font-family: sans-serif;
}

/* Fixed-width because of ad banners */
.col-sidebar { width: 310px; 
    &>* { margin-bottom: 30px; 
        &:last-child { margin-bottom: 0; }
    }
}
.col-sidebar-off {
    &:first-child { left: 10px; position: relative; }
    &:last-child { left: -10px; position: relative; }
}
.col-minus-sidebar-1 { width: calc(100% - 310px); }
.col-minus-sidebar-2 { width: calc(100% - 620px); }

