.widget-news-article-mobile { background-color: $color-bg; padding: 10px 10px 0;

    .article-photo { margin-top: 10px; }
    
    /* General styles in layout/typography */
    .editor-content { margin: 10px 0; }

    /* Video */
    .article-video { margin-top: 30px; } 

    /* Tags */
    .article-tags { margin-top: 20px;

        li { display: inline-block; margin-right: 5px; margin-bottom: 5px; vertical-align: top; }
        a { @include transition-fast; align-items: center; background-color: $color-bg-1; display: flex; height: 40px; line-height: 40px; padding: 0 10px; }
        a:hover { background-color: $color-bg-2; }

        .image { border-radius: 20px; margin-right: 10px; max-height: 40px; max-width: 40px; }
        .label { color: $color-text; display: inline-block; font-size: $font-body-xm; 
            &::before { content: '#'; }
        }
    }
}

/* Social media share links */
.widget-news-article-share { align-items: center; display: flex; margin: 10px 0;
    .share-count { color: $color-article-share; font-size: $font-heading-md; font-weight: bold; }
    .share-divider { background: url(../image/icon/i_slash.png) no-repeat center; height: 50px; margin: 0 10px; width: 12px; }
    
    .share-media { display: flex; }
    .share-media-item { margin-right: 5px;
        a { background-position: center; background-repeat: no-repeat; display: block; height: 30px; width: 30px; }
    }
    .share-media-like { background-image: url(../image/icon/s_FB_like.png); width: 100px !important; }
    .share-media-like-small { background-image: url(../image/icon/s_FB_like_small.png); width: 70px !important; }
    .share-media-facebook { background-image: url(../image/icon/s_FB_share.png); width: 100px !important; }
    .share-media-twitter { background-image: url(../image/icon/s_Twitter_share.png); }
    .share-media-whatsapp { background-image: url(../image/icon/s_Whatsapp_share.png); }
    .share-media-google { background-image: url(../image/icon/s_Google_share.png); }
    .share-media-mail { background-image: url(../image/icon/s_Mail_share.png); }

    .share-comments { margin-left: auto;
        .icon { margin-right: 5px; vertical-align: middle; }
        .label { color: $color-primary; display: inline-block; font-size: $font-body-xm; font-weight: bold; }
    }
}

/* Related News */
.widget-news-article-related { margin: 30px 0;

    .heading { color: $color-primary; line-height: 1em; margin-bottom: 20px; }

    .news-list-link { align-items: center; display: flex; margin-bottom: 10px; padding: 0 3px; }
    .icon {  }
    .title { @include transition-fast; color: $color-text; font-size: $font-body-xm; font-weight: bold; margin-left: 10px; 
        &:hover { color: $color-primary; }
    }
}

/* Article authors / views count */
.widget-news-article-footer { align-items: center; background-color: $color-bg-1; display: flex; justify-content: space-between; margin-left: -10px; margin-right: -10px; margin-top: 30px; padding: 5px 10px;

    .article-author { @include transition-fast; align-items: center; display: flex;
        
        &:hover { color: $color-primary; }

        .photo { border-radius: 30px; margin-right: 10px; max-height: 60px; max-width: 60px; }
        .text-wrap {  }
        .name { font-weight: bold; line-height: 1em; }
        .position { font-size: $font-body-sm; }
    }

    .article-views { align-items: center; display: flex;
        .label { font-size: $font-body-sm; font-weight: bold; margin-right: 10px; }
        .icon {  }
        .count { border-left: 1px solid $color-bg-3; font-size: $font-body-xm; font-weight: bold; height: 40px; line-height: 40px; margin-left: 10px; padding-left: 10px; }
    }
}

/* Embedded tweet */
.widget-news-article-tweet { margin: 20px 0; text-align: center;
    &>* { display: inline-block !important; }
}