/* As in mobile News - open comments button */
.btn-generic { @include transition-fast; background-color: $color-bg; border: 1px solid $color-gray-1; border-radius: 5px; color: $color-text; display: inline-block; font-size: $font-body-md; margin: 15px auto; min-width: 200px; padding: 9px 15px; text-align: center;

	.arrow-prev, .arrow-next { @include transition-fast; background-position: center; background-repeat: no-repeat; display: inline-block; height: 10px; width: 7px; }
	.arrow-prev { background-image: url(../image/icon/ar_previous_normal_smaller.png); margin-right: 7px; }
	.arrow-next { background-image: url(../image/icon/ar_next_normal_smaller.png); margin-left: 7px; }

	&:hover { background-color: $color-primary; border-color: transparent; color: $color-text-inverted; 
		
		.arrow-prev { background-image: url(../image/icon/ar_previous_hover_smaller.png); }
		.arrow-next { background-image: url(../image/icon/ar_next_hover_smaller.png); }
	}
	
}

/* Simple link, as in Team - Overview - Rankings */
.btn-link { @include transition-fast; background: none; border: none; display: block; font-size: $font-body-xm; padding: 15px 0; text-align: center;
	&:hover { color: $color-primary; }
}

/* "Add to Favorite" button, as in Team - Overview */
.btn-favorite { display: inline-block;

	// After section heading, as in Team - News */
	.widget-heading-small + &, .widget-heading-large + & { margin-top: 25px; }

	&:hover .label { color: $color-primary; }
	.icon { margin-right: 5px; vertical-align: middle; }
	.label { @include transition-fast; vertical-align: middle; }
}