/*

    Default Styles for checkradios
	
*/
.checkradios-checkbox, .checkradios-radio {
	
	position:relative;
	display:inline-block;
	text-align:center;
	margin-right: 5px;
	
	border:1px solid $color-text;
	width:19px;
	height:19px;
	cursor:pointer;
	
	-moz-box-sizing: content-box;
	-webkit-box-sizing: content-box;
	box-sizing: content-box;
	
	overflow:hidden;
	vertical-align: middle;
}


.checkradios-radio {
	-moz-border-radius: 50%;
	-webkit-border-radius: 50%;
	border-radius: 50%;
}


.icon-checkradios-checkmark, .icon-checkradios-circle{
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	
	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


/*Icon Placeholder*/
.checkradios-checkbox:after,.checkradios-radio:after{
    
	content:"";
	height:100%;
	width:100%;
	position:relative;
	display:block;

}

.icon-checkradios-checkmark { border: none; height: 21px; width: 21px;
	&::before { background-color: $color-primary; content: ''; display: inline-block; height: 100%; width: 100%; }
}
.icon-checkradios-circle { border: none; height: 21px; width: 21px;
	&::before { border: 7px solid $color-primary; border-radius: 50%; content: ''; display: inline-block; height: 7px; width: 7px; }
}



/*Hide original*/
.checkradios-checkbox input[type=checkbox], .checkradios-radio input[type=radio]{
    
	margin:0;
	position:absolute;
	top:0;
	left:-9999%;
	width:100%;
	height:100%;
	
	position:absolute;
	display:inline-block;

}