/* Team vs Team logos/names */
.match-teams-generic { align-items: center; display: flex; flex-wrap: wrap; justify-content: center; margin: 10px 0;

	.team-logo { max-height: 50px; max-width: 50px; }
    .team-name { font-size: $font-body-md; font-weight: bold; }
    .match-result { font-size: $font-heading-xm; font-weight: bold; }
	.match-time { font-size: $font-body-sm; margin-top: 2px; }
	
	.match-status { text-align: center; width: 130px;
		/* Only child */
		&:first-child { margin-top: 15px; }
		/* Last counter */
		&:last-child:not(:first-child) { width: 100%; }
	}
	.match-count { color: $color-primary; font-size: 36px; font-weight: bold; line-height: 1em; margin-bottom: 5px; padding-bottom: 5px; position: relative;
		/* Divider line */
		&::after { background-color: $color-primary; content: ''; bottom: 0; display: block; height: 2px; left: 50%; margin-left: -90px; position: absolute; width: 180px; }
	}
}

/* Team side-by-side comparisons */
.match-compare-generic { display: flex;

	&.main {
		.bar, .team, .center { position: relative;
			&::after { background-color: $color-gray-1; bottom: 0; content: ''; height: 1px; left: 0; position: absolute; width: 100%; } 
		}
	}
	&.footer { padding: 15px 0 25px;
		.bar, .team { align-self: flex-start; }
		.bar { text-align: center; width: 110px; }
		.team { flex: 1; padding: 0; }
		.center { align-items: flex-start; }
		.match-symbols { margin-top: 15px; }
		.link { display: inline-block; font-size: $font-body-xs; line-height: 1.2em; margin-top: 5px; }
	}

	.bar { margin: 0 5px; position: relative; width: 50px; 
		.match-symbols { align-self: flex-start; justify-self: flex-start; left: 50%; position: relative; transform: translateX(-50%); white-space: nowrap; }
	}
	.bar-label { display: block; font-size: $font-body-lg; font-weight: bold; height: 30px; line-height: 30px; text-align: center; }
	.bar-track { display: flex; flex-direction: column; height: 100px; justify-content: flex-end; position: relative;
		&::before { border: 1px dashed $color-gray-3; bottom: 0; content: ''; left: 0; position: absolute; right: 0; top: 0; }
	}
	.bar-fill { background-color: $color-bg-1; border: 1px solid $color-gray-1; position: relative; }
	.bar-labels { align-items: center; color: $color-text-gray-3; display: flex; flex-direction: column; height: 100%; padding-top: 30px; text-align: center; }
	.bar-labels-item { flex: 1; }
	

	.team { display: flex; flex: 1; flex-direction: column; justify-content: flex-end; margin-right: 5px; padding: 0 15px 10px; 
		&:last-of-type { align-items: flex-end; margin-left: 5px; margin-right: 0; }
	}
	.team-league {
		.name { font-size: $font-body-xs; font-weight: bold; line-height: 1.2em; margin-top: 3px; }
		.logo { height: 30px; width: 30px; }
	}

	.center { align-items: flex-end; display: flex; justify-content: center; margin: 0 5px; width: 110px; 
		img { margin-bottom: 10px; }
	}
}

.match-season-text { font-size: $font-body-xs; line-height: 1.5em; margin-bottom: 15px; text-align: center;
	.content { font-weight: bold; }
}

/* Circular W, D, L symbols */
.match-symbols { text-align: center;
	.match-outcome { @include circle(20); color: $color-text-inverted; display: inline-block; font-size: $font-body-xs; }
	.match-w { background-color: $color-live; }
    .match-d { background-color: $color-gray-3; }
	.match-l { background-color: $color-finished; }
}

/* Match statistics */
.match-stats { padding: 10px 0 20px;

	.entry { line-height: 1.5em; margin-bottom: 3px; text-align: center; }
	.label { display: inline-block; text-align: right; width: 40%; }
	.divider { @include circle(6); background-color: $color-primary; margin: 0 10px; vertical-align: middle; }
	.content { display: inline-block; font-weight: bold; text-align: left; width: 40%; 
		a { @include transition-fast; }
		a:hover { color: $color-primary; }
	}
}

/* Details */
.widget-match-details { background-color: $color-bg-1; padding-top: 10px; }

/* Head 2 Head */
.widget-match-head { background-color: $color-bg;

	/* Pie charts */
	.match-chart { display: flex; justify-content: center; padding-bottom: 20px; }
	.match-chart-entry { margin: 0 -15px; position: relative; text-align: center; 

		.count { display: inline-block; font-size: $font-body-lg; font-weight: bold; left: 0; padding: 0 10px; position: absolute; text-align: center; top: 20px; width: 100%; }
		.label { display: block; font-size: $font-body-xs; padding: 0 20px; width: 120px; }
	}
	
	.circle { @include circle(120); border: 1px dashed $color-gray-3; position: relative; }
	.circle-fill { background-color: $color-bg-1; border: 1px solid $color-gray-1; border-radius: 50%; display: inline-block; left: 50%; position: absolute; top: 50%; transform: translate(-50%, -50%);

		/* Central dot */
		&::after { @include circle(4); background-color: $color-text; content: ''; left: 50%; margin-left: -2px; margin-top: -2px; position: absolute; top: 50%; z-index: 1; }
	}
}

/* Goals */
.widget-match-goals { background-color: $color-bg;
	
	.match-stats { padding: 25px 15px 20px; text-align: center;
		.entry { display: flex; justify-content: center; margin-bottom: 2px; }
		.label { margin: 0 5px; text-align: center; width: 110px; }
		.content { margin: 0 5px; width: 50px; font-weight: bold; text-align: center; }
		.won { color: $color-primary; }
	}
}

.widget-match-form { background-color: $color-bg;
	.bar-labels-item { border-top: 1px solid $color-gray-1; font-size: $font-body-sm; width: 70px; }
}

.widget-match-ranking { background-color: $color-bg; 
	.bar { display: flex; flex-direction: column; }
	.bar-item { background-color: $color-bg-1; border: 1px solid $color-gray-1; font-size: $font-heading-md; height: 35px; line-height: 35px; margin-top: -1px; text-align: center;
		
		&.primary { background-color: $color-primary; border-color: $color-primary; color: $color-text-inverted; font-weight: bold; }
		&.secondary { background-color: $color-bg; color: $color-primary; font-weight: bold; }
	}
	.bar-labels { justify-content: center; padding-top: 0; }
	.bar-labels-item { @include vertical-align; }
}

/* Top scorers */
.widget-match-topscorers { background-color: $color-bg;

	.box-generic { padding-bottom: 10px; }

	.match-people { display: flex; justify-content: center; margin-bottom: 20px; padding: 0 10px; }
	.match-col { border-bottom: 1px solid $color-gray-1; flex: 1; padding-bottom: 10px; }
	.match-people-spacer { align-items: center; display: flex; flex-direction: column; justify-content: flex-end; margin: 0 10px; padding-bottom: 10px; width: 150px; 
		img { max-width: 50px; }
	}

	.player-entry { display: flex; margin-bottom: 10px; }
	.player-text { align-self: flex-end; flex: 1; font-size: $font-body-xs; }
	.player-name { font-weight: bold; line-height: 1em; }
	.player-image { line-height: 1px; position: relative; }
	.player-photo { border-radius: 50%; height: 40px; position: relative; width: 40px; }
	.player-rank { @include circle(20); background-color: $color-primary; bottom: 0; color: $color-text-inverted; font-size: $font-body-xs; position: absolute; z-index: 1; }

	.match-people-left { padding-right: 20px; text-align: right; 
		.player-text { padding-right: 10px; }
		.player-rank { right: -10px; }
	}
	.match-people-right { padding-left: 20px;
		.player-text { padding-left: 10px; }
		.player-rank { left: -10px; }
	}
}