.widget-league-scorer {
	
	.widget-category-dropdown { padding: 20px 10px; }

	/* Generic */
	.sc-table {  }
	.sc-row { align-items: center; display: flex; padding: 10px; }
	.sc-table-head { border-bottom: 1px solid $color-gray-1;
		.sc-row { padding-bottom: 5px; padding-top: 5px; }
		.sc-player { padding-left: 70px; }
	}
	.sc-table-body .sc-row { 
		&:nth-child(2n+1) { background-color: $color-bg-1; }
		&:hover { color: $color-primary; }
		
		.sc-goals { font-weight: bold; }
	}

	/* Cells */
	.sc-field-small { min-width: 50px; padding: 0 10px; text-align: center; 
		&:first-child { padding-left: 0; }
	}
	.sc-player { align-items: flex-end; display: flex; flex: 1; }
	.sc-player-photo { border-radius: 30px; margin-right: 10px; max-height: 60px; max-width: 60px; }
	.sc-player-text { @include transition-fast; padding-bottom: 4px; }
	.sc-player-name { display: block; font-weight: bold; line-height: 1.3em; }
	.sc-player-team { display: block; line-height: 1.3em; }
}