/************************************************
 * Global header for small devices
 ************************************************/
.widget-header-mobile { align-items: center; background-color: $color-bg; display: flex; height: 44px;

	.icon-menu { background: none; border: none; display: block; height: 44px; padding: 0 10px;
		.line { background-color: $color-gray-3; display: block; height: 2px; margin-bottom: 5px; width: 16px; 
			&:last-child { margin-bottom: 0; }
		}
	}
	.logo {
		img { display: block; }
	}
}
/* Nav bar - current page, back, share */
.widget-header-mobile-breadcrumb { align-items: center; background-color: $color-bg; box-shadow: 0 3px 5px rgba(0,0,0,0.33); color: $color-text-inverted; display: flex; height: 40px; line-height: 40px; padding: 0 10px; position: relative; z-index: 1;
	.current { font-size: $font-body-xm; font-weight: bold; line-height: 1.2em; }
	.back { width: 25px;
		.icon { vertical-align: middle; }
	}
	.share {  margin-left: auto;
		.label { color: $color-text-gray-3; font-size: $font-body-xs; margin-right: 8px; vertical-align: middle; }
		.icon { vertical-align: middle; }
	}

	/* Nav bar - transparent, as in Gallery photo */
	&.header-transparent { background-color: $color-text-opaque; left: 0; position: fixed; top: 0; width: 100%; z-index: 10; }
	.gallery-status-mobile { font-size: $font-body-xm; justify-self: center; margin-left: auto;
		.gallery-current { color: $color-primary; font-weight: bold; }
		.gallery-total { }
	}

	/* Watch live, as in Team Details */
	.watch-live { margin-left: auto;
		.label { color: $color-live; }
		.icon { margin-left: 5px; vertical-align: middle; }
	}
}

/* Category dropdown inside header */
.header-mobile-select { margin-left: auto; position: relative;

	&::after { background: url(../image/icon/ar_caret.png) no-repeat center; content: ''; height: 100%; position: absolute; padding: 0 10px; right: 0; top: 0; z-index: 0; }
	select { @include vendor(appearance); background: none; border: none; color: $color-text-gray-3; font-size: $font-body-xm; height: 28px; line-height: 28px; padding: 0 20px 0 10px; position: relative; z-index: 1; }
	option { color: $color-text; }
}


/************************************************
 * Drawer menu
 ************************************************/
.widget-header-mobile-nav { background-color: $color-text-1; bottom: 0; box-shadow: inset -5px 0 10px $color-text-opaque; color: $color-text-inverted; left: -240px; overflow: auto; padding: 0 10px; position: fixed; top: 0; width: 240px; z-index: 10;

	.item-wrap { border-bottom: 1px solid $color-text-2; }

	.item-top { font-size: $font-body-xm; font-weight: bold; line-height: 1.2em; padding: 12.5px 10px; position: relative;

		.icon { @include transition-fast; @include vertical-align; background-size: auto; height: 44px; padding: 0 10px; position: absolute; right: 0; top: 0;
			&::after { @include circle(16); background: $color-primary url(../image/icon/ar_expand.png) no-repeat center; background-size: auto; content: ''; display: inline-block; vertical-align: middle; }
		}
		&.active .icon { transform: rotate(-180deg); }
	}
	.item-top-link { @include transition-fast; display: block; 
		&:hover { color: $color-primary; }
	}

	/* Specific list styles */
	.sub-text-list { margin-bottom: 10px;
		.sub-item-link { color: $color-text-gray-3; font-size: $font-body-sm; font-weight: bold; line-height: 1.2em; padding: 8px 0 8px 30px; }
		
	}
	.sub-thumb-list {
		.sub-item-link { align-items: center; display: flex; padding-left: 15px; }
		.logo { margin-right: 10px; max-height: 30px; max-width: 30px; }

	}
	.sub-thumb-grid { margin-bottom: 10px;
		.sub-item { display: inline-block; margin-bottom: 5px; vertical-align: top; width: 32%; }
		.sub-item-link { line-height: 1px; text-align: center; }
		.logo { max-height: 50px; max-width: 50px; }
		.title { color: $color-text-gray-3; display: block; font-size: $font-body-xs; line-height: 1.2em; margin-top: 5px; }
	}
	.sub-thumb-players {
		.logo { border-radius: 25px; }
	}

	.sub-item-link { @include transition-fast; display: block; 
		&:hover { color: $color-primary; }
	}


	/* Generic show/hide styles */
	.item-dropdown { margin-bottom: 0; }
	.active .item-dropdown { margin-bottom: 10px; }
}

/* Classes are applied on <body> via javascript */
body { @include transition-medium(true); }
body.mobile-nav-open { overflow: hidden; transform: translateX(240px); 
	.mobile-nav-overlay { visibility: visible; }
}
/* Invisible overlay - users can click content to close the menu */
.mobile-nav-overlay { height: 100%; left: 0; position: absolute; top: 0; visibility: hidden; width: 100%; z-index: 2; }
