/************************************************
 * Stacked news in sidebar
 ************************************************/
.widget-news-sidebar { background-color: $color-bg; margin-top: 10px;

    .item { border-bottom: 1px solid $color-bg-2; display: block; padding: 10px 10px 12px; position: relative; 

        &:last-child { border-bottom: none; }
        &:hover {
            .category, .title { color: $color-primary; }
        }

        .icon-play { left: 15px; top: 15px; }
    }

    /* Item inside grid column, as in 480 News */
    .grid { padding-left: 10px; padding-right: 10px; }
    .col-1 {
        .item { padding-left: 0; padding-right: 0; }
        .icon-play { left: 5px; }
    }

    .photo { display: block; }
    .category { @include transition-fast; color: $color-text-gray-3; font-size: $font-body-md; display: block; margin-top: 7px; text-transform: uppercase; }
    .title { @include transition-fast; }
    //.time { @include transition-fast; color: $color-text-gray-3; display: block; font-size: $font-body-sm; margin-top: 5px; }
}