/************************************************
 * Override default air-datepicker styles
 ************************************************/

 /* Global */
 .datepicker { background-color: $color-bg-1; border-radius: 0; width: 310px; }

 /* Inline calendar in sidebar */
 .col-sidebar .datepicker { box-sizing: border-box; max-width: 300px; }
 /* Inline calendar in sidebar or anywhere else */
 .datepicker-inline .datepicker { background-color: transparent; border: none; border-bottom: 1px solid $color-gray-1; padding-bottom: 30px;
	.datepicker--content { padding: 0 3px; }
}



 /* Headings */
 .datepicker--nav { border-bottom: none; padding: 15px 30px 0; }
 .datepicker--nav-title { color: $color-text; }
 .datepicker--nav-action { @include transition-fast; background-color: $color-gray-3; border-radius: 16px;
	
	path { @include transition-fast; stroke: $color-text-inverted; }

	&:hover { background-color: $color-primary; }
}
.datepicker--days-names { margin: 15px 0 10px; }
.datepicker--day-name { color: $color-text; font-size: $font-body-xs; }

 

 /* Cells */
 .datepicker--content { padding: 0 10px 30px; }
 .datepicker--cell { font-size: $font-body-xm; height: 40px; line-height: 40px; margin: 1px; width: calc( 14.28571% - 2px ); }
 .datepicker--cell:not(.-other-month-) { background-color: $color-bg; border: 1px solid $color-gray-1; color: $color-text; }
 .datepicker--cell.-other-month- { color: $color-text-gray-3; }

 .datepicker--cell.-focus-, 
 .datepicker--cell.-selected-,
 .datepicker--cell.-in-range-,
 .-selected-.-focus-.datepicker--cell-day,
 .-selected-.-focus-.datepicker--cell-day.-other-month-, 
 .-selected-.-focus-.datepicker--cell-year.-other-decade-, 
 .-selected-.-focus-.datepicker--cell-day.-other-month-, 
 .-selected-.-focus-.datepicker--cell-year.-other-decade-, 
 .-selected-.datepicker--cell-day.-other-month-, 
 .-selected-.datepicker--cell-year.-other-decade,
 .-selected-.-focus-.datepicker--cell-day.-other-month-, 
 .-selected-.-focus-.datepicker--cell-year.-other-decade- { background-color: $color-primary; border-color: transparent; color: $color-text-inverted; }

 .datepicker--cell.-current-, .datepicker--cell.-current-.-in-range-, .datepicker--button { color: $color-primary; }

 /* Calendar "button" */
.calendar-trigger { padding: 5px 0; position: relative; text-align: center;
    .label {  display: inline-block; vertical-align: middle; }
    .label-pale { color: $color-text-gray-3; vertical-align: middle; }
    .icon { @include transition-cheetah; border: none; margin-right: 7px; vertical-align: middle; }

    /* Faux invisible input overlay, since air-datepicker plugin needs an input */
    input { cursor: pointer; height: 100%; left: 0; opacity: 0; position: absolute; top: 0; width: 100%; z-index: 2; }
}

/* Range calendar "button", as in mobile Search results */
.widget-calendar-range { display: flex; justify-content: space-between; margin-top: 10px; padding: 5px 10px; position: relative;

    .calendar-trigger-range { background: none; border: none; height: 100%; left: 0; opacity: 0; position: absolute; top: 0; width: 100%; }
    .calendar-date { display: inline-block; }
    .label { vertical-align: middle; }
    .icon { margin: 0 10px; vertical-align: middle; }
    .result { background: none; border: none; vertical-align: middle; width: 70px; }

    @include media-sm {
        padding: 5px 30px;
        .result { width: 80px; }
    }
}