/* Extends .widget-prediction for mobile devices */
.widget-carousel-prediction { border-bottom: 1px solid $color-gray-1; margin-top: 10px; padding-bottom: 30px; position: relative;

	.content { display: block; }
	.match-date { font-size: $font-heading-xm; }
	.team-name { font-size: $font-body-md; }
	.prediction-panel-invite { font-size: $font-body-md; }

	/* Controls */
	.slick-dots { bottom: -50px; display: flex; justify-content: center; left: 0; position: absolute; width: 100%;

		li { font-size: 0; line-height: 0; margin: 0 2.5px; }
		button { background: $color-bg-2; border: none; font-size: 0; height: 4px; text-indent: -9999px; width: 60px; }
		.slick-active button { background-color: $color-primary; }
	}

	/* Odds */
	.match-bet-wrap { border-bottom: 1px solid $color-gray-1; }
	.match-bet { border-bottom: none; }
	.bet-odds { font-size: $font-body-xm; }
}