/************************************************
 * Generic form elements
************************************************/
.form-entry { margin-bottom: 20px; text-align: left; }
.form-input, .form-textarea { background-color: $color-bg-1; border: 1px solid $color-gray-1; color: $color-text; display: inline-block; font-weight: bold; padding: 8px 10px; width: 400px; }
.form-input {  }
.form-textarea { min-height: 160px; }
.form-label { display: inline-block; margin-right: 20px; margin-top: 7px; min-width: 120px; text-align: right; vertical-align: top; }
.form-select { background-color: $color-bg-1; display: inline-block; position: relative;
    &::after { background: url(../image/icon/ar_expand.png) no-repeat center; content: ''; height: 100%; position: absolute; padding: 0 20px; right: 0; top: 0; z-index: 0; }
    select { @include vendor(appearance); background: transparent; border: 1px solid $color-gray-1; color: $color-text; font-size: $font-body-md; font-weight: bold; height: 40px; line-height: 40px; min-width: 200px; padding: 0 20px; position: relative; z-index: 1; }
}
.form-select-label { display: inline-block; margin-right: 20px; }

/* Submit button */
button[type="submit"] { line-height: inherit; }
.form-submit { @include transition-fast; background: $color-bg; border: 1px solid $color-gray-1; border-radius: 3px; height: 40px; line-height: 40px; margin-top: 15px; min-width: 200px; padding: 0 20px;
    &:hover { background-color: $color-primary; border-color: transparent; color: $color-text-inverted; }
}