.widget-league-scorer {

	/* Table */
	.sc-table { font-size: $font-body-xs; margin-bottom: 20px; }
	.sc-row { padding: 5px; }
	.sc-table-head { background-color: $color-bg;
		.sc-row { padding-bottom: 2px; padding-top: 1px; }
		.sc-player { padding-left: 45px; }
	}
	.sc-table-body {
		.sc-row { background-color: $color-bg; margin-bottom: 1px;
			&:nth-child(2n+1) { background-color: $color-bg; }
		}
	}
	/* Fields */
	.sc-field-small { min-width: 36px; padding: 0 5px; text-align: center; 
		&:first-child { padding-right: 0; }
	}
	.sc-player-photo { border-radius: 20px; margin-right: 5px; max-height: 40px; max-width: 40px; }
}