/************************************************
 * Thin tabs, as in Football Match
 ************************************************/
.widget-tabs-lined { margin-top: 30px;

    .tab-nav { background-color: $color-bg; display: flex; border-top: 1px solid $color-gray-1;

        li { flex: 1; text-align: center; }
        a { @include transition-fast; border-bottom: 2px solid transparent; color: $color-text-gray-3; display: block; font-size: $font-body-sm; height: 24px; line-height: 22px; }
        a:hover { border-color: $color-primary; }
        .active a { border-color: $color-primary; color: $color-text; }

        /* More items icon */
        .tab-nav-more { flex-grow: 0; margin-left: auto;
            .icon { display: inline-block; width: 44px;
                span { @include transition-fast;  background-color: $color-gray-3; display: inline-block; height: 2px; margin-left: 2px; width: 2px; }
            }
    
            &:hover {
                .icon { border-bottom: none;
                    span { background-color: $color-primary; }
                }
            }
        }
    }

    .tab-content { background-color: $color-bg-1; padding: 20px 10px; }
    
}

/* No padding/background for mobile */
.widget-tabs-lined-mobile { margin-top: 0;
    .tab-content { background-color: transparent; padding: 0; }
}

.widget-match-inline, .widget-match-inline-mobile {
    .widget-tabs-lined { margin-left: -10px; margin-right: -10px; }
}