.widget-team-vote { margin-top: 20px; padding: 0 10px;

	.title { margin-bottom: 15px; }
	.vote-wrap { align-items: center; display: flex; }
	.vote-rating { align-items: center; display: flex;
		.value { background-color: $color-primary; color: white; font-size: $font-heading-xl; font-weight: bold; height: 45px; line-height: 23px; margin-right: 20px; padding: 10px; text-align: center; width: 55px; }
	}
	.vote-action { border-left: 1px solid $color-gray-1; margin-left: auto; padding-left: 20px; padding-right: 10px; }
	.vote-count { margin-top: 10px; text-align: center; }

	.vote-results {  }
	.vote-results-row { align-items: center; display: flex; line-height: 1px; }
	.vote-results-stars { text-align: right; transform: scale(0.6); width: 70px; }
	.vote-results-track { background-color: $color-bg; display: block; height: 1px; width: 100px; }
	.vote-results-fill { background-color: $color-gray-4; display: block; height: 100%; }

	@include media-sm {
		.vote-rating {
			.value { font-size: $font-heading-xxxl; height: 90px; line-height: 70px; width: 110px; }
		}
		.vote-action { @include vertical-align; height: 90px; }
		.btn-link { display: inline-block; height: 100%; padding: 0 20px; }
		
		.vote-results-row { margin-bottom: 8px; }
		.vote-results-stars { margin-right: 20px; transform: none; }
		.vote-results-track { height: 2px; width: 200px; }
	}
}

/* Team Info */
.widget-team-info { background-color: $color-bg;

	.team-properties { padding: 20px;

		.entry { line-height: 1.5em; margin-bottom: 5px; }
		.label { display: inline-block; text-align: right; width: 43%; }
		.divider { @include circle(6); background-color: $color-primary; margin: 0 12px; vertical-align: middle; }
		.content { display: inline-block; font-weight: bold; width: 43%; 
			a { @include transition-fast; }
			a:hover { color: $color-primary; }
		}
	}
	
}

/* Next Match */
.widget-team-match { padding-bottom: 20px; }

/* Team form - bad, good, etc. */
.widget-team-form { background-color: $color-bg; padding-bottom: 10px;

	.bar-percentage { font-size: $font-body-xm; font-weight: bold; margin-top: 15px; text-align: center; }
	.bar-wrap { display: flex; padding: 8px 10px 20px; position: relative; }
	.bar-logo { border-bottom: 1px solid $color-gray-1; flex: 1; line-height: 1px; text-align: center;
		img { height: 50px; width: 50px; }
	}
	.bar-labels { border-bottom: 1px solid $color-gray-1; display: flex; flex: 1; flex-direction: column; align-items: center; }
	.bar-labels-item { @include vertical-align; border-top: 1px solid $color-gray-1; color: $color-text-gray-3; flex: 1; text-align: center; width: 60%; }

	.bar-track { display: flex; flex-direction: column; justify-content: flex-end; height: 100px; margin: 0 10px; position: relative; width: 50px; 
		/* Dotted border */
		&::before { border: 1px dashed $color-gray-3; bottom: 0; content: ''; left: 0; position: absolute; right: 0; top: 0; }
	}
	.bar-fill { background-color: $color-bg-1; border: 1px solid $color-gray-1; position: relative; }
	
	@include media-sm {
		.bar-percentage { font-size: $font-body-lg; }
	}
}

/* As in Team - Overview */
.widget-team-ranking { background-color: $color-bg; padding-bottom: 0;

    .bar-league { font-size: $font-body-xm; padding: 20px 10px 0; text-align: center;
        /* As in Team - Matches - Fixtures */
        .logo { max-height: 30px; max-width: 30px; }
		.title { font-weight: bold; margin-top: 3px; }
		.date { margin: 5px 0 10px; }
	}
	
	.bar-wrap { padding-bottom: 10px; }
	.bar-track { height: auto; 
		&::before { content: normal; }
	}
	.bar-list-item { background-color: $color-bg-1; border: 1px solid $color-gray-1; font-size: $font-body-lg; line-height: 35px; height: 35px; margin-top: -1px; text-align: center; width: 50px; 
		&.primary { background-color: $color-primary; border-color: $color-primary; color: $color-text-inverted; font-weight: bold; }
		&.secondary { background-color: $color-bg; font-weight: bold; }
	}
	.bar-labels-item { border-top: none; }

	/* Top scorer, etc. */
	.team-ranking-players { background-color: $color-bg-1; margin-top: 10px; padding: 0 20px;

		.entry { align-items: flex-end; display: flex; margin: 10px 0; }
		.player-image { margin-right: 5px; position: relative; }
		.player-event { bottom: 0; left: -10px; position: absolute; }
		.player-photo { border-radius: 30px; display: block; max-height: 40px; max-width: 40px; }
		.player-name { font-size: $font-body-xs; font-weight: bold; line-height: 1em; }
		.player-position { font-size: $font-body-xs; }
		.player-rank { color: $color-primary; font-weight: bold; }
	}

	@include media-sm {
		.bar-league { font-size: $font-body-lg; }

		.team-ranking-players {
			.player-event { left: -13px; }
			.player-photo { max-height: 60px; max-width: 60px; }
			.player-name { font-size: $font-body-xm; }
			.player-position { font-size: $font-body-xm; }
		}
	}
}

.widget-team-squad {

	/* Season */
	.widget-category-dropdown { padding: 20px 10px 0; }
	
	.squad-header { font-weight: bold; height: 30px; line-height: 30px; margin-top: 20px; padding: 0 10px; }
	.squad-list { background-color: $color-bg-1;
		.player-container { border-bottom: 1px solid $color-bg-global; position: relative; }
		.player { align-items: flex-end; display: flex; padding: 5px 10px; }
		.player-image { line-height: 1px; position: relative; }
		.player-photo { border-radius: 50%; height: 40px; margin-right: 10px; width: 40px; }
		.player-rank { @include circle(20); background-color: $color-finished; color: $color-text-inverted; margin-right: -15px; position: relative; vertical-align: bottom;  z-index: 1; }
		.player-text { align-self: flex-end; flex: 1; padding-bottom: 5px; }
		.player-name { font-weight: bold; line-height: 1em; }
		.player-favorite { margin-top: -15px; position: absolute; right: 3px; top: 50%; }
	}
}