/************************************************
 * Embedded prediction poll
 ************************************************/
.widget-prediction-poll-mobile { background-color: $color-bg; margin: 0 -10px 10px;

    .prediction-content { background-color: $color-bg; padding: 15px 10px 10px; position: relative; text-align: center; }

    .match-league { color: $color-primary; font-size: $font-body-xm; font-weight: bold; }
    .match-week { font-size: $font-body-xs; }
    .watch-live { position: absolute; right: 10px; top: 5px;
        .icon { vertical-align: middle; }
        .label { color: $color-live; font-size: $font-body-md; margin-right: 5px; }
    }

    .match-teams { align-items: center; display: flex; justify-content: center; margin-top: 10px; }
    .team-name { font-size: $font-body-sm; font-weight: bold; }
    .team-logo { max-height: 50px; max-width: 50px; }

    .match-status { margin: 0 50px; }
    .match-result { font-size: $font-heading-xm; font-weight: bold; }
    .match-time { font-size: $font-body-sm; margin-top: 5px; }


    /* Vote */
    .prediction-vote { margin-top: 15px;

        /* Results */
        &:first-child { margin-top: 0; }

        .vote-heading { font-size: $font-body-md; }
        .vote-panel { display: flex; justify-content: center; margin-top: 20px; }
        .vote-item { list-style: none; margin: 0 5px; }
        .vote-button { @include transition-fast; background: none; border: 3px solid transparent; border-radius: 15px; color: $color-text-inverted; font-size: $font-heading-xm; height: 30px; line-height: 25px; padding: 0; width: 80px; }
        .vote-home { background-color: $color-live;
            &:hover { background-color: $color-bg; border-color: $color-live; color: $color-live; }
        }
        .vote-draw { background-color: $color-gray-3; 
            &:hover { background-color: $color-bg; border-color: $color-gray-3; color: $color-text-gray-3; }
        }
        .vote-away { background-color: $color-primary; 
            &:hover { background-color: $color-bg; border-color: $color-primary; color: $color-primary; }
        }
    }
    .vote-count { font-size: $font-body-md; margin-bottom: 0; margin-top: 15px; }
    .vote-count-results { margin-top: 16px; }

    /* Results */
    .vote-results { display: flex; margin: 10px auto 0; }
    .vote-results-item { margin: 0 2.5px; position: relative;
        .icon { margin-bottom: 5px; }
        .bar { color: $color-text-inverted; font-size: $font-body-md; height: 20px; line-height: 22px; padding: 0 10px; }
        .name { font-size: $font-body-sm; font-weight: bold; margin-top: 15px; position: absolute; }
    }
    .vote-results-home { text-align: left;
        .bar { background-color: $color-live; border-radius: 4px 0 0 4px; }
        .name { left: 0; }
    }
    .vote-results-draw {
        .bar { background-color: $color-gray-3; }
    }
    .vote-results-away { text-align: right;
        .bar { background-color: $color-primary; border-radius: 0 4px 4px 0; }
        .name { right: 0; }
    }
    
    @include media-sm {
        .match-league { font-size: $font-heading-xm; }
        .match-week { font-size: $font-body-md; margin-top: 5px; }
        .watch-live { top: 10px;
            .label { font-size: $font-body-xm; }
        }
        .team-name { font-size: $font-body-xm; }
        .match-result { font-size: $font-heading-xl; }
        
        .prediction-vote {
            .vote-heading { font-size: $font-body-xm; }
        }
        .vote-count { font-size: $font-body-xm; }
        .vote-results-item {
            .name { font-size: $font-body-xm; }
        }
    }
}