/* Simple structural heading */
.widget-heading-small { background-color: $color-bg; border-bottom: 4px solid $color-primary; display: flex; margin-bottom: 0; padding: 16px 10px 12px; }

/* Structural heading with tabs, etc. */
.widget-heading-large { align-items: center; background-color: $color-bg; border-bottom: 4px solid $color-primary; display: flex; margin-bottom: 0; margin-top: 30px; min-height: 80px; padding: 10px 10px 6px;

    &:first-child { margin-top: 0; }
    .icon { max-height: 60px; margin-right: 15px; max-width: 60px; }
}

/* Heading integrated in content */
.widget-heading-plain { align-items: center; display: flex; margin-bottom: 10px; padding-left: 10px; padding-right: 10px;
    .category-logo { max-height: 60px; margin-right: 20px; max-width: 60px; }
    .heading-tabs { position: relative; top: -2px; }
}


/* Subsections */
.heading-tabs { display: flex; flex: 1; margin-left: 30px; margin-top: 7px;
    li { margin-right: 20px; }
    a { @include transition-cheetah; color: $color-text-secondary; }
    a:hover, .active a { color: $color-primary; }
    .active a { font-weight: bold; }

    /* Right-aligned (last) element */
    .right { margin-left: auto; margin-right: 0; }

    /* Calendar */
    .calendar-trigger { position: relative;
        .label { color: $color-text-secondary; display: inline-block; margin-right: 5px; vertical-align: top; }
        .icon { @include transition-cheetah; border: none; margin-right: 0; margin-top: -5px; vertical-align: bottom; }

        /* Faux invisible input overlay, since air-datepicker plugin needs an input */
        input { cursor: pointer; height: 100%; left: 0; opacity: 0; position: absolute; top: 0; width: 100%; z-index: 2; }

        &:hover { 
            .label { color: $color-primary; }
            .icon { background-image: url(../image/icon/calendar_hover.png); }
        }
    }

    .toggle-switch { margin-top: -5px; }
}

/* Team logos inside heading */
.heading-team { align-self: flex-end; margin-left: auto; margin-right: 10px;
    .heading-team-item { display: inline-block; margin-left: 20px; }
    a { display: block; }
    img { display: block; max-height: 60px; max-width: 60px;  }
}