.widget-header-mobile {

    .widget-header-util { margin-left: auto;
        li { margin: 0; }
        a { height: 44px; opacity: 0.65; padding: 10px 7px 0; }
        .current a { background-color: $color-primary; opacity: 1; 
            .label { color: $color-text-inverted; }
        }
        .icon { background-size: contain; height: 16px; width: 16px; }
        .label { color: $color-text-gray-3; font-size: $font-body-xxs; }
    }

    // Don't show icon labels > 480
    @include media-sm-down {
        .widget-header-util {
            a { padding: 0 7px; }
            .icon { height: 44px; }
            .label { display: none; }
        }
    }
}