.widget-footer-default { background-color: $color-bg; border: 1px solid $color-gray-1; color: $color-text-secondary; display: flex; flex-wrap: wrap; font-size: $font-body-sm; justify-content: space-between; margin-top: 30px; padding: 20px 10px;

    .footer-left { width: 50%; }
    .footer-right { text-align: right; width: 50%; }

    .footer-logo {
        img { display: block; }
    }
    .footer-copyright { margin-top: 10px; }
    .footer-social {
        a { display: inline-block; margin-left: 10px; }
    }
    .footer-links {
        a { @include transition-fast; display: inline-block; margin-left: 20px; 
            &:hover { color: $color-primary; }
        }
    }
}