/************************************************
 * Utility navigation
 ************************************************/
.widget-header-util { height: 50px;

    ul { align-items: center; display: flex; height: 100%; }
    li { height: 100%; }
    a { @include transition-fast; align-items: center; display: flex; flex-direction: column; height: 100%; justify-content: center; padding: 0 10px; text-align: center; 
        &:hover { opacity: 0.7; }
    }
    .current a { background-color: $color-primary; 
        .label { color: $color-text-inverted; }
    }
    .label { color: $color-text-gray-1; display: block; font-size: $font-body-xs; font-weight: bold; line-height: 7px; margin-top: 5px; }
    .icon { height: 24px; width: 24px; }
}