/************************************************
 * Common
 ************************************************/
* { box-sizing: border-box; -webkit-font-smoothing: antialiased; -moz-osx-font-smoothing: grayscale; }
html, body { min-height: 100%; height: 100%; }
body { background-color: $color-bg-global; color: $color-text; }

.global-wrap { clear: both; overflow: hidden; }
.wrap-width { margin: 0 auto; max-width: 1080px; position: relative; }
.wrap-padding { padding-left: 10px; padding-right: 10px; }
a { color: inherit; text-decoration: none; }
img { max-height: 100%; max-width: 100%; }
button { cursor: pointer; }
button::-moz-focus-inner { padding: 0; border: 0 }
*:focus {  }



/************************************************
 * Force an element to contain its children
 ************************************************/
.clearfix::after, .grid::after { clear: both; content: " "; display: block; font-size: 0; height: 0; visibility: hidden; }
* html .clearfix, * html .grid { zoom: 1; } /* IE6 */
*:first-child+html .clearfix, *:first-child+html .grid { zoom: 1; } /* IE7 */