.widget-comments { padding-top: 10px !important;

	.comment-entry { background-color: $color-bg; border: 1px solid $color-gray-1; margin-left: 10px; margin-right: 10px; padding: 15px; }
	.comment-header { align-items: flex-end; margin-bottom: 7px; }
	.comment-username { font-size: $font-body-xm; }
	.comment-number { align-self: center; font-size: $font-body-xm; }
	.comment-avatar { border-radius: 25px; max-height: 50px; max-width: 50px; }

	.comment-reply { margin-left: 11%; padding-top: 15px; }
}

.news-comments-trigger { margin-top: 10px; text-align: center; }


/* Add Comment */
.comment-add {

    .comment-add-form { display: block; padding: 0 10px; text-align: center;

		.warning-image { margin-top: 20px; text-align: center; }
		.form-input, .form-textarea { background-color: $color-bg; }
        .form-submit { float: none; margin-top: 20px; }
    }
}