// Create a circle
@mixin circle($size) {
	border-radius: 50%;
	display: inline-block;
	height: #{$size}px;
	line-height: #{$size}px;
	text-align: center;
	width: #{$size}px;
}

// Center items vertically
@mixin vertical-align {
	position: relative;
	&::before { content: ''; display: inline-block; height: 100%; vertical-align: middle; }
	img { vertical-align: middle; }
}

// Add vendor prefixes
@mixin vendor($property, $value: 'none') {
	#{$property}: #{$value};
	-moz-#{$property}: #{$value};
	-webkit-#{$property}: #{$value};
	-o-#{$property}: #{$value};
	-ms-#{$property}: #{$value};
}

// Transitions
@mixin transition-snail($bezier: false, $delay: 0) {
	transition: all 1000ms ease-in-out #{$delay}ms;
	@if $bezier {
		transition-timing-function: cubic-bezier(1,0,0,1);
	}
}
@mixin transition-slow($bezier: false, $delay: 0) {
	transition: all 750ms cubic-bezier(1,0,0,1) #{$delay}ms;
	@if $bezier == false {
		transition-timing-function: ease-in-out;
	}
}
@mixin transition-medium($bezier: false, $delay: 0) {
	transition: all 500ms cubic-bezier(1,0,0,1) #{$delay}ms;
	@if $bezier == false {
		transition-timing-function: ease-in-out;
	}
}
@mixin transition-fast($bezier: false, $delay: 0) {
	transition: all 200ms cubic-bezier(1,0,0,1) #{$delay}ms;
	@if $bezier == false {
		transition-timing-function: ease-in-out;
	}
}
@mixin transition-cheetah($bezier: false, $delay: 0) {
	transition: all 70ms cubic-bezier(1,0,0,1) #{$delay}ms;
	@if $bezier == false {
		transition-timing-function: ease-in-out;
	}
}
