.widget-comments {

    .comment-entry { margin-top: 5px; padding: 20px; 
        &:first-of-type { margin-top: 0; }
    }
    .comment-content {
        .media { margin: 15px 0; }
    }

    /* User date */
    .comment-header { align-items: center; display: flex; margin-bottom: 15px; }
    .comment-avatar { border-radius: 30px; margin-right: 10px; max-height: 60px; max-width: 60px; }
    .comment-user-wrap {  }
    .comment-username { font-weight: bold; line-height: 1em; }
    .comment-time { font-size: $font-body-sm; }
    .comment-number { font-weight: bold; justify-self: flex-end; margin-left: auto; }

    /* Reply / Like / Dislike */
    .comment-footer { align-items: baseline; color: $color-text-gray-3; font-size: $font-body-sm; display: flex; margin-top: 13px; 
        a { @include transition-fast; }
        a:hover { color: $color-text; }
    }
    .comment-footer-like {
        .icon { background-image: url(../image/icon/i_like.png); height: 20px; position: relative; top: 3px; width: 16px; }
        &.active { color: $color-live;
            .icon { background-image: url(../image/icon/i_like_hover-active.png); }
        }
    }
    .comment-footer-dislike {
        .icon { background-image: url(../image/icon/i_dislike.png); height: 20px; position: relative; top: 10px; width: 16px; }
        &.active { color: $color-finished;
            .icon { background-image: url(../image/icon/i_dislike_hover-active.png); }
        }
    }
    .comment-footer-icon { margin: 0 2px; }
    .comment-footer-middot { margin: 0 10px; }
    
    /* Nested reply */
    .comment-reply { border-top: 1px solid $color-gray-1; margin-top: 25px; margin-left: 15%; padding-top: 20px; }

    /* Load more comments button */
    .widget-view-more-load { margin-top: 20px; }
}

/* Add Comment */
.comment-add {

    .comment-add-form { display: inline-block; padding: 30px;

        /* Login to upload an image */
        .warning-image { margin-bottom: 0; margin-top: -5px;
            .icon { vertical-align: middle; }
            .text { color: $color-text-gray-3; font-size: $font-body-sm; margin-left: 7px; vertical-align: middle;
                a { color: $color-text; font-weight: bold; text-decoration: underline; }
            }
        }
        .form-submit { float: right; margin-top: 20px; }
    }
}