.widget-odds-mobile { display: block; margin-top: 5px;
    
    .team-logo { height: 40px; margin: 0; width: 40px; }
    
    .match-bet-wrap { border-bottom: 1px solid $color-gray-1; }
    .match-bet { border-bottom: none; display: flex; padding: 0 10px;
    
        li { margin: 0 2.5px; text-align: center; 
            &:first-child .bet-odds { border-top-left-radius: 5px; }
            &:last-child .bet-odds { border-top-right-radius: 5px; }
        }
    }
    .bet-label { display: block; font-size: $font-body-sm; font-weight: bold; }
    .bet-odds { background-color: $color-bg; border: 1px solid $color-gray-1; border-bottom: none; display: inline-block; font-size: $font-body-xm; padding: 5px 15px; }
    .odds-provider { display: block; text-align: center;
        img { display: inline-block; }
    }
}

/* Odds inside news article */
.widget-news-article { 
    .widget-odds { border-bottom: none; }
}

/* Odds inside prediction vote */
.widget-prediction-poll-mobile {
    .widget-odds { background-color: $color-bg-1; margin-left: -10px; margin-right: -10px; margin-top: 5px; padding-top: 10px; }
}

/* Odds inside Team Details */
.widget-match-intro {
    .widget-odds { background-color: $color-bg-1; padding: 10px; margin-top: 15px; }
}

/* Odds inside LiveScores */
.widget-livescore-list {
    .widget-odds { border-bottom: none; margin-left: -10px; margin-top: 10px;
        .odds-provider { width: 77px; }
        .match-bet-wrap { margin-left: -57px;; }
        .match-bet li { margin: 0; 
            &:nth-child(2n) .bet-odds { background: $color-bg-2; }
        }
        .bet-odds { border: none; border-radius: 0 !important; display: block; font-size: $font-body-xxs; height: 15.5px; line-height: 15.5px; padding: 0 10px; }
        .bet-label { color: $color-bets; display: inline-block; font-size: $font-body-xxs; }

        @include media-sm {
            .bet-odds, .bet-label { font-size: $font-body-xs; }
        }
    }    
}