.widget-carousel-2 { background-color: $color-bg; position: relative;

    .slick-slide { margin: 0 5px; }
    .content { display: flex; padding: 10px 5px; }
    .entry { flex: 1; position: relative; 
        &:hover .title { background-color: $color-primary; }
    }
    .live { background-color: $color-primary; bottom: 0; color: $color-text-inverted; font-size: $font-body-sm; left: 0; padding: 3px 10px 3px 5px; position: absolute; z-index: 1;
        .icon { height: 16px; vertical-align: middle; width: 16px; }
        .label { display: inline-block; line-height: 1em; margin-left: 2px; position: relative; text-transform: uppercase; }
    }
    .image { background-position: center; background-repeat: no-repeat center; background-size: cover; padding-bottom: 60%; position: relative; }
    .title { @include transition-fast; font-size: $font-body-md; font-weight: bold; line-height: 1.3em; margin-top: 5px; }
}
