/* Dates carousel */
.widget-carousel-livescore-dates { background-color: transparent; margin-top: 20px;

    .content { margin: 10px 30px; }
    .slick-list { border: none; margin: 0; }
    .slick-slide { margin: 0 1px; }
    .entry {
        &:not(.active-date):hover { background-color: transparent; border-color: transparent; color: $color-primary; }
    }

    .day { font-size: $font-body-md; }
    .date { font-size: $font-body-xm; font-weight: bold; }

    .arrows {
        button { width: 30px; }
    }
}


.widget-livescore-list { margin-bottom: 20px;

    /* No background, as in Team - Fixtures */
    &.widget-livescore-list-team { background-color: transparent; }

    /* As in Fixtures */
    .section-header-small { font-size: $font-body-xs; }
    .toggle-switch { margin-left: auto; }

    .entry { border-bottom: 1px solid $color-text-inverted; margin-bottom: 0; }
    .status { font-size: $font-body-xs; line-height: normal; padding-top: 2px; }
    .team-home { padding-left: 5px; }
    .team-away { padding-right: 5px; }
    .team-name { font-size: $font-body-xs; line-height: 1.3em; margin-left: 7px; margin-right: 7px; }

    /* Result or upcoming time */
    .dashboard-result { font-size: $font-body-md; width: 55px; }
    .watch { right: 5px; }

    /* Formula 1 players */
    .f1-player-list { padding: 0 15px; }
    .f1-player { font-size: $font-body-xs; margin: 0 5px; }
    .f1-upcoming-time { font-size: $font-body-md; }

    @include media-sm {
        /* As in Fixtures */
        .section-header-small { font-size: $font-body-sm; }

        .status { font-size: $font-body-xm; }
        .section-header {
            .name { font-size: $font-body-xm; }
            .league { font-size: $font-body-xm; }
        }
        .team-name { font-size: $font-body-xm; }
        .dashboard-result { font-size: $font-heading-xm; width: 75px; }
    }
}