/* Generic heading with gray border */
.widget-heading-generic { align-items: center; background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; display: flex; margin-top: 10px; padding: 15px 10px 15px; }

/* Simple structural heading with accent border */
.widget-heading-small .title { font-size: $font-heading-xl; font-weight: normal; }

/* Mobile Category page */
.widget-heading-mobile-league { align-items: center; display: flex; padding: 10px;

	.league-logo, .player-photo { max-height: 50px; margin-right: 10px; max-width: 50px; }
	.player-photo { border-radius: 50%; }

	.league-text { align-self: flex-end; flex: 1; }
	.league-name { font-weight: bold; }
	.league-location { font-size: $font-body-xs; line-height: 1.2em; }	
}

.favorite-generic { background: url(../image/icon/i_favorite_off.png) no-repeat center; height: 30px; width: 30px;
	&.active { background-image: url(../image/icon/i_favorite_on.png); }
}