//================================================
// Sidebar standings
//================================================
.widget-standings-list { margin-bottom: 20px; margin-top: 30px;

    &:first-of-type { margin-top: 0; }

    .st-table { border: 1px solid $color-gray-1; width: 100%; }
    /* Header */
    .st-table-head { border-bottom: 1px solid $color-gray-1; color: $color-text-secondary;
        .row>* { background-color: $color-bg; line-height: 1.3em; padding-bottom: 5px; padding-top: 7px; }
        .st-team { padding-left: 50px; }
    }
    .row { align-items: center; border-right: 4px solid transparent; display: flex;
        &>* { @include transition-fast; padding: 10px; text-align: center; }
    }
    /* Main */
    .st-table-body {
        .row:nth-child(2n+1) { background-color: $color-bg-1; }

        /* When the team is a link */
        a.row { @include transition-fast;

            &:hover, &.row-active { border-right-color: $color-primary;
                .st-team-name, .st-points { color: $color-primary; font-weight: bold; }
            }
        }
    }
    
    .st-position { line-height: 30px; text-align: center; width: 50px; }
    .st-team { @include vertical-align; flex: 1; padding-left: 0; text-align: left; }
    .st-team-logo { margin-right: 10px; max-height: 30px; max-width: 30px; }
    .st-team-name { @include transition-fast; }
    .st-matches { margin-right: 20px; width: 140px; }
    .st-match-outcome { @include circle(20); color: $color-text-inverted; display: inline-block; font-size: $font-body-xs; }
	.match-w { background-color: $color-live; }
    .match-d { background-color: $color-gray-3; }
	.match-l { background-color: $color-finished; }
    .st-goals { width: 100px; }
    .st-field-small { width: 30px; }
    .st-points { width: 50px; }
    

    /* Footer - View More, color legend, etc. */
    .st-table-foot { background-color: $color-bg; border-top: 1px solid $color-gray-1; padding-bottom: 10px; padding-top: 10px; }
    .widget-view-more { flex: 1; margin: 0; text-align: right; }


    /* Accordion */
    .st-table-accordion {
        .st-table-body { @include transition-fast; border-bottom: 1px solid $color-gray-1; max-height: 0; overflow: hidden; 
            &:last-of-type { border-bottom: none; }
        }
    }
    .st-table-group { @include transition-fast; align-items: center; cursor: pointer; display: flex; padding: 5px 10px;

        &:hover { background-color: $color-bg-1; }

        .name { font-weight: bold; }
        .icon { @include transition-fast; background: url(../image/icon/ar_expand.png) no-repeat center; background-size: cover; height: 6px; margin-left: auto; width: 10px; }

        &.open {
            .icon { background-image: url(../image/icon/ar_close.png); }
            & + .st-table-body { max-height: 1000px; }
        }
    }

    /* Color Codes */
    .st-color-legend { padding: 15px 10px 5px;
        .legend-row { align-items: center; display: flex; margin-bottom: 10px; }
        .legend-color { display: inline-block; height: 15px; margin-right: 10px; width: 15px; }
        .legend-meaning {  }
    }
}

//================================================
// Dedicated league Standings table
//================================================
.widget-league-standings { background-color: $color-bg;

    /* Season / Game Week */
    .widget-category-dropdown { padding: 20px 10px; }
    
    .st-table { border: none; }
}


