/* Search results form */
.widget-search-form { @include transition-fast; background-color: $color-text; display: flex; justify-content: center; left: 0; opacity: 0; position: absolute; top: 0; visibility: hidden; width: 100%; z-index: 1;

	&.active { opacity: 1; visibility: visible; }

	.icon { height: 44px; margin: 0 10px; }
	.keywords { background: none; border: none; border-bottom: 1px solid $color-text-2; color: $color-text-inverted; flex: 1; height: 44px; font-size: $font-body-xm; line-height: 44px; padding: 0 10px; }
	.search-close { background: transparent url(../image/icon/search_close.png) no-repeat center; border: none; display: inline-block; height: 44px; padding: 0; width: 32px; }
}


/* Player profile in search results */
.widget-player-profile { background-color: $color-bg; display: block; padding: 10px 10px 50px; position: relative;

	.profile-image { display: inline-block; position: relative;
		.photo { border-radius: 50px; display: block; max-height: 100px; max-width: 100px; }
		.number { @include circle(30); background-color: $color-primary; bottom: 0; color: $color-text-inverted; font-size: $font-body-md; font-weight: bold; position: absolute; right: 0; }
	}

	.rating { align-items: center; display: flex; margin-top: 20px;
		.value { background-color: $color-primary; color: white; font-size: $font-heading-xl; font-weight: bold; line-height: 1em; margin-right: 10px; padding: 10px; }
	}

	.profile-name { font-size: $font-heading-xs; font-weight: bold; line-height: 1.3em; padding-top: 10px; }
	.profile-team { font-size: $font-body-xs; line-height: 1.3em; }
	.profile-properties { margin-top: 20px;

		.entry { line-height: 1.5em; margin-bottom: 5px; }
		.label { display: inline-block; text-align: right; width: 40%; }
		.divider { @include circle(6); background-color: $color-primary; margin: 0 18px; vertical-align: middle; }
		.content { display: inline-block; font-weight: bold; width: 40%; }
	}

	.widget-view-more { bottom: 0; position: absolute; right: 0; }

	@include media-sm {
		.profile-image {
			.photo { border-radius: 75px; max-height: 150px; max-width: 150px; }
		}
		.profile-name { font-size: $font-heading-lg; }
		.profile-team { font-size: $font-body-sm; }
		.profile-properties { font-size: $font-body-xm; 
			.entry { margin-bottom: 8px; }
		}
	}
}