/* Generic table styles for player tables */
.player-table { font-size: $font-body-xs;

	.table-row { align-items: center; display: flex; }
	.table-head { border-bottom: 1px solid $color-gray-1; 
		.table-row { height: 20px; line-height: 20px; }
	}
	.table-body { background-color: $color-bg; border-bottom: 1px solid $color-bg-2; 
		.table-row { border-bottom: 1px solid $color-bg-2; }
	}
	.table-foot {
		.table-row { justify-content: flex-end; }
	}

	.cell { align-items: center; flex: 1; padding: 4px 10px; text-align: center; }
	.cell-flex { display: flex; }
	/* Alignments */
	.cell-left { text-align: left; }
	.cell-right { text-align: right; }

	/* Widths */
	@for $i from 1 through 10 {
		.cell-#{$i} { flex: #{$i}; }
	}

	/* Specific elements */
	.logo-small { align-self: flex-start; height: 30px; margin-right: 10px; vertical-align: middle; width: 30px; }
	.logo-large { height: 60px; margin-right: 10px; vertical-align: middle; width: 60px; }
	.icon { height: 20px; margin-right: 10px; vertical-align: middle; width: 20px; }
	.flag { margin-right: 5px; }
	.text { align-self: flex-end; display: flex; flex-direction: column; line-height: 1em; padding-bottom: 10px; vertical-align: middle; }
	.name { font-size: $font-body-sm; font-weight: bold; }
	.date { margin-top: 10px; }
	.price { font-weight: bold; margin-top: 10px; }
	.sub { margin-top: 4px; }
	.empty { padding-bottom: 0; padding-top: 0; }
}

/* Career */
.widget-player-career { margin-top: 30px;
	
	.table-foot { 
		.cell { background-color: $color-primary; color: $color-text-inverted; font-weight: bold; }
		.empty { background-color: $color-bg; }
	}
}

/* Transfers */
.widget-player-transfers { margin-top: 15px;

	.table-body .cell { padding-bottom: 12px; padding-top: 12px; }
	.text { padding-bottom: 0; }
}

/* Trophies */
.widget-player-trophies { margin-top: 20px;

	.section-header { padding-top: 0; }
	.trophy-list { background-color: $color-bg-1; margin-bottom: 20px; padding: 15px;

		.trophy-entry { line-height: 1.5em; margin-bottom: 20px; 
			&:last-child { margin-bottom: 0; }
		}
		.label { display: inline-block; font-weight: bold; }
		.divider { @include circle(6); background-color: $color-primary; margin-left: 8px; margin-right: 12px; vertical-align: middle; }
		.content { display: block; margin-top: 10px; padding-left: 30px;
			a { @include transition-fast; }
			a:hover { color: $color-primary; }
		}
		strong { font-weight: bold; }
	}
}

/* Matches */
.widget-player-matches { background-color: transparent;
	.section-header {
		&:first-child { padding-top: 0; }
	}
}

/* Sidelined */
.widget-player-sidelined { margin-top: 20px;
	.table-body .cell { padding: 15px 10px; }
}