/* As in Competitions - Popular */
.widget-team-list {
	
	.entry { width: 100%; }
	.logo { width: 50px;
		img { max-height: 50px; max-width: 50px; }
	}
	.text { padding-left: 10px; }
}

/* As in Competitions - Club <...>, National Teams */
.widget-team-accordion {
	
	.section-header .name { font-size: $font-body-xm; }
	.section-content { font-size: $font-body-xm; 
		.entry { display: block; }
		.name { line-height: 1.3em; padding-top: 3px; width: 100%; }
		.edition { color: $color-text-gray-3; font-size: $font-body-md; line-height: 1.3em; width: 100%; }
	}
}

/* As in League - Teams */
.widget-team-list-vertical {

	.list { padding: 10px 15px; }
	.entry { flex-basis: 20%; padding: 10px 15px; }
	.logo img { max-height: 70px; max-width: 70px; }
	.name { font-size: $font-body-xs; }

	@include media-sm {
		.logo img { max-height: 100px; max-width: 100px; }
		.name { font-size: $font-heading-xs; }
	}
}