/* As in Competitions - Popular */
.widget-team-list { background-color: $color-bg; display: flex; flex-wrap: wrap; padding: 20px 10px 0;
	
	.entry { display: flex; align-items: flex-end; margin-bottom: 20px;
		&:hover .text { color: $color-primary; }
	}
	.logo { text-align: center; width: 60px;
		img { max-height: 60px; max-width: 60px; }
	}
	.text { @include transition-fast; padding-bottom: 10px; padding-left: 20px; }
}

/* As in Competitions - Club <...>, National Teams */
.widget-team-accordion {
	
	.team-section { background-color: $color-bg-1; }
	.section-header { @include transition-fast; align-items: center; background-color: $color-bg; border-bottom: 1px solid $color-gray-1; cursor: pointer; display: flex; height: 50px; padding: 10px 10px 0; position: relative;

		&:hover { color: $color-primary; }

		/* Country flag */
        .flag { align-items: center; display: flex; border: 1px solid $color-bg-2; height: 16px;
            /* Image instead of flag icon fong */
            img { display: block; max-width: 20px; }
        }
		.name { font-weight: bold; margin-left: 10px; margin-right: 4px; }
		
		/* Expand / contract */
		.icon { @include transition-fast; background: url(../image/icon/ar_expand.png) no-repeat center; height: 100%; position: absolute; right: 0; top: 0; width: 40px; }
		&.open { background-color: transparent; border-bottom-color: transparent;
			.icon { transform: rotate(-180deg); }
		}
    }

	.section-content { background-color: $color-bg-1; padding: 10px 10px 10px 40px;
		.entry { @include transition-fast; display: flex; margin-bottom: 10px; 
			&:hover { color: $color-primary; }
		}
		.name { width: 50%; }
		.edition { width: 50%; }
	}
}

/* As in League - Teams */
.widget-team-list-vertical { background-color: $color-bg;

	/* Season dropdown */
	.widget-category-dropdown { padding: 20px 10px 0; }

	.list { display: flex; flex-wrap: wrap; justify-content: space-between; padding: 20px 10px; }
	.entry { flex-basis: 20%; padding: 15px 20px; text-align: center; 
		&:hover .name { color: $color-primary; }
	}
	.logo { line-height: 1px;
		img { max-height: 100px; max-width: 100px; }
	}
	.name {  @include transition-fast; font-weight: bold; line-height: 1.2em; margin-top: 7px; }
}