/************************************************
 * White background container, spacing corrections
 ************************************************/
.box-generic-mobile { background-color: $color-bg; margin: 10px 0;
    .widget-news-grid { padding: 0; 
        .item { margin-bottom: 0; }
    }

    &>.widget { border-top: 1px solid $color-bg-2; padding: 10px; 
        &:first-child { border-top: none; }
    }
}