/************************************************
 * Helpers - margin / padding classes
 ************************************************/
 @for $i from 0 through 10 {
	.mt-#{$i} { margin-top: ($size-spacer * $i) !important; }
	.mb-#{$i} { margin-bottom: ($size-spacer * $i) !important; }
	.pt-#{$i} { padding-top: ($size-spacer * $i) !important; }
	.pb-#{$i} { padding-bottom: ($size-spacer * $i) !important; }
}

@include media-xm {
	@for $i from 0 through 10 {
		$size-spacer-large: $size-spacer * $i * 2;
		.mt-#{$i} { margin-top: ($size-spacer-large) !important; }
		.mb-#{$i} { margin-bottom: ($size-spacer-large) !important; }
		.pt-#{$i} { padding-top: ($size-spacer-large) !important; }
		.pb-#{$i} { padding-bottom: ($size-spacer-large) !important; }
	}
}