//================================================
// Fonts
//================================================
$font-family-default: "'Open Sans', Helvetica, Arial, sans-serif";

$font-body-lg: 16px;
$font-body-xm: 14px;
$font-body-md: 12px;
$font-body-sm: 11px;
$font-body-xs: 10px;
$font-body-xxs: 8px;

$font-heading-xxxl: 48px;
$font-heading-xxl: 26px;
$font-heading-xl: 24px;
$font-heading-lg: 20px;
$font-heading-xm: 18px;
$font-heading-md: 16px;
$font-heading-sm: 15px;
$font-heading-xs: 14px;
$font-heading-xxs: 12px;



//================================================
// Media query sizes
//================================================
$width-xl: 1600px; // Large desktop
$width-lg: 1280px; // Desktop
$width-xm: 1024px; // Notebook
$width-md: 768px; // Tablet
$width-sm: 480px; // Mobile
$width-xs: 320px; // Tiny mobile



//================================================
// Miscellaneous sizes
//================================================
$size-spacer: 5px; // Multiplier for general margin, padding classes, i.e. mt-3 = margin-top: $spacer * 3
$size-wrap: 30px;   // Side margin for small devices
