/* Stars rating - common styles */
.icon-stars { background-repeat: repeat-x; background-position: left center; display: inline-block; position: relative;

	&>* { background-repeat: repeat-x; background-position: left center; display: inline-block; left: 0; position: absolute; top: 0; }
	.stars-1, &.stars-1 { width: 20%; }
	.stars-2, &.stars-2 { width: 40%; }
	.stars-3, &.stars-3 { width: 60%; }
	.stars-4, &.stars-4 { width: 80%; }
	.stars-5, &.stars-5 { width: 100%; }
}
/* Small stars rating, as in Search results player profile */
.icon-stars-small { background-image: url(../image/icon/star_small_empty.png); height: 10px; width: 70px; 
	&>* { background-image: url(../image/icon/star_small.png); height: 10px; }
}
/* Large star ratings, as in Team Overview */
.icon-stars-large { background: url(../image/icon/star_large_empty.png); height: 16px; width: 112px; 
	&>* { background-image: url(../image/icon/star_large.png); height: 16px; }
}

/* Stars rating as in Team Overview - vote results */
.icon-stars-results { background-image: url(../image/icon/star_results.png); background-position: right center; height: 10px; width: 70px;
	&>* { background-position: right center; }
}