/************************************************
 * Checkbox, transformed into IOS-like toggle switch
 ************************************************/
.toggle-switch { margin-left: auto;

    .toggle-switch-label { cursor: pointer; display: block; height: 24px; position: relative; width: 60px;
        &::before { @include transition-fast; background-color: $color-text-1; border-radius: 12px; content: ''; display: block; height: 24px; left: 0; position: absolute; top: 0; width: 60px; }
        &::after { @include transition-fast; background-color: $color-bg; color: $color-text; content: ''; border-radius: 11px; display: block; font-size: $font-body-xs; font-weight: bold; height: 22px; line-height: 22px; left: 1px; position: absolute; text-align: center; top: 1px; width: 40px; }
    }

    .toggle-switch-input { display: none; 

        /* Selected */
        &:checked + .toggle-switch-label {
            &::before { background-color: $color-live; } 
            &::after { left: 19px; }
        }
    }
}

/* Content */
.toggle-switch-odds {
    .toggle-switch-label::after { content: attr(data-content); }
}

/* Inside mobile global header */
.widget-header-mobile-breadcrumb {
    .toggle-switch-label::before { border: 1px solid $color-text-2; height: 22px; width: 58px; }
}