/************************************************
 * Match inside an article, as in News
 ************************************************/
.widget-match-inline-mobile { font-size: $font-body-md; margin-top: 30px;

    .match-heading { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; margin-left: -10px; margin-right: -10px; padding: 16px 10px; }
    .match-inline-heading  { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; padding: 10px 10px; 

        .title { font-weight: normal; }
    }

    /* Match Details */
    .match-info { padding: 15px 20px; position: relative; text-align: center; }
    .match-league { color: $color-primary; font-size: $font-body-xm; font-weight: bold; }
    .match-week { font-size: $font-body-sm; }
    .match-teams { align-items: center; display: flex; justify-content: center; margin-top: 10px; }
    .match-team { text-align: center; }

    .team-name { font-size: $font-body-xs; font-weight: bold; }
    .team-logo { max-height: 50px; max-width: 50px; }
    .match-status { width: 220px; }
    .match-result { font-size: $font-heading-xm; font-weight: bold; }
    .match-time { font-size: $font-body-sm; margin-top: 5px; }

    /* Timeline */
    .match-details { padding-bottom: 15px; }
    .match-timeline-item { align-items: center; display: flex; padding: 5px 0; }
    .timeline-event { align-items: flex-end; display: flex; flex: 1; font-size: $font-body-xs; line-height: 1.3em;
        &:first-child { justify-content: flex-end;
            .icon {right: 0; }
        }
        &:last-child .icon { left: 0; }
    }
    .timeline-event-image { position: relative;
        .photo { border-radius: 20px; max-height: 40px; max-width: 40px; }
        .icon { bottom: 0; display: block; position: absolute; }
    }
    .timeline-time { @include circle(20); background-color: $color-primary; color: $color-text-inverted; font-size: $font-body-xs; margin: 0 10px; }
    .timeline-event-text { margin: 0 5px; padding-bottom: 5px;

        &:first-child { text-align: right; }
        .primary { font-weight: bold; }
        .secondary {  }
    }

    .match-timeline-section { border-bottom: 1px solid $color-gray-1; line-height: 1em; margin-bottom: 5px; padding: 6px 10px; text-align: center;
        .icon { margin-bottom: 2px; }
        .title { font-size: $font-body-xs; font-weight: bold; }
    }

    /* Stadium / Referee */
    .match-meta-info { align-items: flex-start; display: flex; font-size: $font-body-md; line-height: 1.2em; justify-content: center; padding-top: 10px;
        .label { flex: 1; text-align: right; }
        .dot { @include circle(6); background-color: $color-primary; margin: 4px 10px 0; }
        .title { flex: 1; font-weight: bold; }
    }

    /* Goals */
    .match-goals { background-color: $color-bg-1; margin-left: -10px; margin-right: -10px; }
    .match-goals-list { font-size: $font-body-md; }
    .match-goals-item { align-items: center; display: flex; }
    .goal-player { align-items: center; background-color: $color-bg; display: flex; flex: 1; font-size: $font-body-xs; height: 40px; line-height: 1.2em; margin-bottom: 2px; padding: 0 5px;

        &:first-child { justify-content: flex-end; text-align: right;
            .replay { justify-self: flex-start; margin-right: auto; }
        }
        &:last-child { justify-content: flex-start; 
            .replay { justify-self: flex-end; margin-left: auto; }
        }

        .name { font-weight: bold; }
        .photo { border-radius: 20px; margin: 0 10px; max-height: 40px; max-width: 40px;  }
        .replay { line-height: 1px; padding: 5px;
            .icon { height: 16px; width: 16px; }
        }

    }
    .goal-result { background-color: $color-bg-2; font-size: $font-body-xm; font-weight: bold; height: 40px; line-height: 40px; text-align: center; width: 50px; }

    @include media-sm {
        .match-league { font-size: $font-heading-xm; }
        .match-week { font-size: $font-body-md; margin-top: 5px; }
        .team-name { font-size: $font-body-xm; }
        .match-result { font-size: $font-heading-xl; }

        .match-meta-info { font-size: $font-body-xm; }
        .match-timeline-section {
            .icon { margin-bottom: 7px; }
            .title { font-size: $font-body-xm; }
        }
        .timeline-event {
            &:first-child .icon { right: -10px; }
            &:last-child .icon { left: -10px; }
        }
        .timeline-time { @include circle(30); font-size: $font-body-xm; margin: 0 20px; }
        .timeline-event-image {
            .photo { border-radius: 30px; max-height: 60px; max-width: 60px; }
        }
        .timeline-event-text {
            .primary { font-size: $font-body-xm; margin-bottom: 5px; }
            .secondary { font-size: $font-body-xm; }
        }

        .goal-player {
            .name { font-size: $font-body-md; margin-bottom: 2px; }
            .minute { font-size: $font-body-md; }
        }
    }
}