.widget-prediction { background-color: $color-bg; margin-top: 30px; padding-bottom: 15px;

    .content { display: flex; margin: 20px 8px 0; }
    .match-entry { flex: 1; }
    .match-entry-prediction { background-color: $color-bg-2; border: 1px solid $color-bg-2; font-size: $font-body-sm; margin: 0 1.5px 2px; position: relative; 
        &>* { position: relative; z-index: 1; }
        &::before { background-color: $color-bg-1; content: ''; height: 40px; left: 0; position: absolute; top: 0; width: 100%; z-index: 0; }
    }

    .match-week { color: $color-text-secondary; padding-top: 2px; text-align: center; }
    .match-teams { display: flex; justify-content: space-evenly; padding: 3px 7px 8px; text-align: center; }
    .team-logo { max-height: 30px; max-width: 30px; }

    .match-time { position: relative; top: 5px; text-align: center; }
    .match-date { font-size: $font-heading-xs; font-weight: bold; line-height: 1em; }
    .match-hour { color: $color-text-secondary; line-height: 1em; margin-top: 2px; }

    .match-teams-names { display: flex; justify-content: center; text-align: center; }
    .team-name-spacer { width: 36px; }
    .team-name { line-height: 1.4em; flex: 1; padding: 0 5px; }

    /* Inputs */
    .prediction-panel { align-items: center; background-color: $color-bg-1; display: flex; flex-wrap: wrap; justify-content: center; padding: 15px 20px; }
    .prediction-entry {  }
    .prediction-input { background: $color-bg-input; border: 1px solid $color-gray-1; font-weight: bold; height: 30px; line-height: 30px; text-align: center; width: 30px; }
    .prediction-input-ctrl { border: none; background: none; background-position: center; background-repeat: no-repeat; display: block; height: 20px; padding: 0; width: 30px; }
    .prediction-input-up { background-image: url(../image/icon/ar_close.png); }
    .prediction-input-down { background-image: url(../image/icon/ar_expand.png); }
    .prediction-panel-dash { font-size: $font-heading-xs; font-weight: bold; margin: 0 20px; }
    .prediction-panel-invite { display: block; font-size: $font-body-sm; margin-top: 5px; text-align: center; width: 100%; }

    /* Odds */    
    .odds-provider { display: block; line-height: 0; text-align: center; width: 100%; }
}