/* Photo galleries overview category; League standings category */
.widget-category-dropdown { margin-bottom: 20px; padding-top: 20px; text-align: center;
    .form-select { background: $color-bg; border-radius: 5px; }
    select { font-size: $font-body-xm; font-weight: normal; }
    select { border-radius: 5px; }
}

/* League standings week */
.widget-category-nav { display: flex; justify-content: center; padding: 0 25px;
    .arrow { @include transition-fast; @include circle(50); background-color: $color-bg; background-position: center; background-repeat: no-repeat; border: 1px solid $color-gray-1;
        &.prev { background-image: url(../image/icon/ar_previous_small_normal.png); }
        &.next { background-image: url(../image/icon/ar_next_small_normal.png); }
    }
    .content { background-color: $color-bg-1; margin: 0 30px; padding: 7px 20px; text-align: center; }
    .label {  }
    .week { font-size: $font-heading-xm; font-weight: bold; }
}