/************************************************
 * Global
 ************************************************/
body, input, textarea, select, button { font: normal #{$font-body-md}/1.5em #{$font-family-default}; }



/************************************************
 * Headings
 ************************************************/
.heading-xl { font-size: $font-heading-xl; line-height: 1.2em; }
.heading-lg { font-size: $font-heading-lg; font-weight: bold; line-height: 1.2em; }
.heading-xm { font-size: $font-heading-xm; font-weight: bold; line-height: 1.2em; }
.heading-md { font-size: $font-heading-md; font-weight: 600; line-height: 1.2em; }
.heading-sm { font-size: $font-heading-sm; font-weight: bold; line-height: 1.2em; }
.heading-xs { font-size: $font-heading-xs; font-weight: bold; line-height: 1.2em; }
.heading-xxs { font-size: $font-heading-xxs; font-weight: bold; line-height: 1.2em; }

@include media-sm {
	body, input, textarea, select, button { font-size: $font-body-xm; line-height: 1.5em; }

	.heading-xm { font-size: $font-heading-xxl; }
	.heading-sm { font-size: $font-heading-xm; }
	.heading-xxs { font-size: $font-heading-xs; }
}


/************************************************
 * WYSIWYG
 ************************************************/
 .editor-content { font-size: $font-body-xm; line-height: 1.5em;
	p { line-height: 1.5em; margin-bottom: 15px; }
	strong { font-weight: bold; }
	a { @include transition-fast; color: $color-primary; }
}