/* All thumbs in a gallery */
.widget-gallery-thumbs-mobile { background-color: $color-bg; margin-bottom: 20px; padding: 10px;

    .gallery-title { line-height: 1.2em; }    
    .gallery-time { color: $color-text-gray-3; display: block; font-size: $font-body-sm; margin-top: 5px; }

    .gallery-counter { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; font-size: $font-body-lg; height: 40px; line-height: 40px; margin: 10px -10px 0; padding: 0 10px;

        .icon { vertical-align: middle; }
        .label { margin-left: 7px; vertical-align: middle; }
        .count { font-weight: bold; }
    }

    .gallery-content { display: flex; flex-wrap: wrap; justify-content: space-between; padding-top: 10px;

        .entry { margin-bottom: 1px; width: calc(33.333% - 0.5px); }
        img { display: block; }
    }
}

/* Open photo */
.widget-gallery-main-mobile { background-color: $color-text-1; height: 100%; left: 0; position: absolute; top: 0; width: 100%; z-index: 1;
    
    .content { height: 100%; }
    * { height: 100%; }
    .entry { background-position: center; background-repeat: no-repeat; background-size: cover; height: 100%; overflow: hidden; }
}

/* Inline photo with gallery items count */
.widget-gallery-trigger-mobile { display: block; margin: 15px 0; position: relative;
    .trigger-photo { display: block; }
}

.gallery-photos-count { background-color: $color-text-opaque; border-radius: 0 3px 0 0; bottom: 0; left: 0; padding: 5px 20px 5px 10px; position: absolute;
    .icon { height: 15px; position: relative; top: -1px; vertical-align: middle; width: 20px; }
    .label { color: $color-text-inverted; font-size: $font-body-xm; font-weight: bold; margin-left: 7px; vertical-align: middle; }
}