/* Photo next to title/date */
.widget-news-grid-horizontal {
    
    .item { margin-bottom: 20px; }
    
    /* Hover line to the right, not bottom */
    .photo::before { height: 100%; left: auto; right: 5px; width: 4px; }
}

.widget-news-grid.widget-news-grid-horizontal-mobile { border-top: none; margin: 10px -5px 0; margin-top: 0; padding-top: 0;

	.item { border-top: 1px solid $color-bg-2; margin-bottom: 0; padding: 5px; 
		&:last-child { padding-bottom: 0; }
	}

    .photo::before { height: 100%; left: auto; right: 5px; width: 4px; }
}