.widget-news-grid { padding-left: 10px; padding-right: 10px;
    
    &:first-child { padding-top: 10px; }
    &:last-child { padding-bottom: 10px; }

    .item { display: block; margin-bottom: 30px; 

        &:hover {
            .photo::before { background-color: $color-primary; }
            .title, .time { color: $color-primary; }
        }
    }
    .photo { position: relative;

        &::before { @include transition-fast; bottom: 0; content: ''; display: block; left: 0; position: absolute; right: 0; height: 4px; z-index: 1; }
        img { display: block; }
    }
    .title { @include transition-fast; line-height: 1.2em; margin-top: 8px; }
    .time { @include transition-fast; color: $color-text-secondary; display: block; font-size: $font-body-sm; margin-top: 5px; }
    .category { @include transition-fast; color: $color-text-gray-3; font-size: $font-body-sm; display: block; margin-top: 2.5px; text-transform: uppercase; }
    .excerpt { margin-top: 10px; }

    /* Dropdown, as in Search */
    .widget-category-dropdown { padding: 10px 20px 20px; }
}
