// 1600 and up
@mixin media-xl {
	@media only screen and (min-width : #{$width-xl}) {
		@content;
	}
}
// 1280 and up
@mixin media-lg {
	@media only screen and (min-width : #{$width-lg}) {
		@content;
	}
}
// Below 1280
@mixin media-lg-down {
	@media only screen and (max-width: #{$width-lg - 1px}) {
		@content;
	}
}
// 1025 and up
@mixin media-xm {
	@media only screen and (min-width : #{$width-xm}) {
		@content;
	}
}
// Below 1025
@mixin media-xm-down {
	@media only screen and (max-width: #{$width-xm - 1px}) {
		@content;
	}
}
// 1025 to 1280
@mixin media-xm-only {
	@media only screen and (min-width: #{$width-xm - 1px}) and (max-width: #{$width-lg - 1px})  {
		@content;
	}
}
// 768 and up
@mixin media-md {
	@media only screen and (min-width: #{$width-md}) {
		@content;
	}
}
// 768 to 1025
@mixin media-md-only {
	@media only screen and (min-width: #{$width-md}) and (max-width: #{$width-xm - 1px}) {
		@content;
	}
}
// Below 768
@mixin media-md-down {
	@media only screen and (max-width: #{$width-md - 1px}) {
		@content;
	}
}

// 480 and up
@mixin media-sm {
	@media only screen and (min-width : #{$width-sm}) {
		@content;
	}
}
// 480 to 768
@mixin media-sm-only {
	@media only screen and (min-width : #{$width-sm}) and (max-width: #{$width-md - 1px}) {
		@content;
	}
}
// Below 480
@mixin media-sm-down {
	@media only screen and (max-width: #{$width-sm - 1px}) {
		@content;
	}
}

// 320 and up
@mixin media-xs {
	@media only screen and (min-width : #{$width-xs}) {
		@content;
	}
}
// 320 to 480
@mixin media-xs-only {
	@media only screen and (min-width : #{$width-xs}) and (max-width: #{$width-sm - 1px}) {
		@content;
	}
}
// Below 320
@mixin media-xs-down {
	@media only screen and (max-width: #{$width-xs - 1px}) {
		@content;
	}
}
