/* Dates carousel */
.widget-carousel-livescore-dates { position: relative;

    .content { display: flex; flex-wrap: wrap; margin: 10px 44px; }
    .slick-list { border-right: 1px solid $color-bg-2; margin: 0 -0.5px; width: 100%; }
    .slick-slide { margin: 0 0.5px; }
    .entry { @include transition-fast; align-items: center; background-color: $color-bg-1; border: 1px solid $color-bg-2; display: flex !important; flex: 1; flex-direction: column; height: 50px; justify-content: center; margin: 0 0.5px; text-align: center; 
        &:not(.active-date):hover { background-color: transparent; border-color: transparent; color: $color-primary; }
    }
    .active-date { background-color: $color-primary; border-color: $color-primary; color: $color-text-inverted; }

    .day { font-size: $font-body-sm; line-height: 1.2em; }
    .date { font-size: $font-body-lg; font-weight: bold; line-height: 1.2em; }

    .arrows {
        button { @include transition-fast; background: none; background-position: center; background-repeat: no-repeat; border: none; display: block; height: 100%; padding: 0; position: absolute; top: 0; width: 40px; }    
        .prev { background-image: url(../image/icon/ar_previous_normal.png); left: 0; 
            &:hover { background-image: url(../image/icon/ar_previous_hover.png); }
        }
        .next { background-image: url(../image/icon/ar_next_normal.png); right: 0; 
            &:hover { background-image: url(../image/icon/ar_next_hover.png); }
        }
    }
}


/* List of match results / scheduled times */
.widget-livescore-list { background-color: $color-bg;

    /* No background, as in Team - Fixtures */
    &.widget-livescore-list-team {
        .section-header { height: 60px; padding-bottom: 5px; }
    }

    /* Season / Game Week, as in Fixtures */
    .widget-category-dropdown { padding: 20px 10px 0; }

    /* As in LiveScores page */
    .section-header { align-items: center; display: flex; height: 50px; padding: 10px 10px 0;
        .flag { align-items: center; display: flex; border: 1px solid $color-bg-2; height: 16px;
            /* Image instead of flag icon fong */
            img { display: block; max-width: 20px; }
        }
        /* As in Team - Matches - Fixtures */
        .logo { max-height: 30px; max-width: 30px; }
        .name { font-weight: bold; margin-left: 10px; margin-right: 4px; }
    }
    /* As in Fixtures */
    .section-header-small { border-bottom: 1px solid $color-gray-1; font-weight: bold; height: 30px; line-height: 30px; margin-top: 20px; padding: 0 10px; }

    .entry { align-items: center; background-color: $color-bg-1; display: flex; flex-wrap: wrap; margin-bottom: 1px; padding: 10px 10px; position: relative; }

    .status { border-left: 5px solid transparent; height: 100%; left: 0; line-height: 50px; padding-left: 5px; position: absolute; top: 0; }
    .status-finished { border-color: $color-finished; color: $color-finished; }
    .status-live { border-color: $color-live; color: $color-live; }

    .team { align-items: center; display: flex; flex: 1; }
    .team-home { justify-content: flex-end; padding-left: 60px; text-align: right; 
        .team-event { justify-self: flex-start; left: -10px; margin-right: auto; }
    }
    .team-away { justify-content: flex-start; padding-right: 60px; text-align: left; 
        .team-event { justify-self: flex-end; margin-left: auto; right: -20px; }
    }

    .team-name { margin-left: 10px; margin-right: 10px; }
    .team-name-active { color: $color-primary; font-weight: bold; }
    .team-logo { max-height: 30px; max-width: 30px; }
    .team-flag { height: 24px; width: 30px; }
    .team-face { border-radius: 15px; max-height: 30px; max-width: 30px; }
    
    .team-event { position: relative;
        .icon { height: 20px; width: 20px; margin: 0 7px; vertical-align: middle; }
        .label { display: inline-block; vertical-align: middle; }
    }

    /* Result or upcoming time */
    .dashboard-result { font-size: $font-heading-xs; font-weight: bold; text-align: center; width: 75px; }

    .watch { @include vertical-align; position: absolute; height: 50px; right: 10px; top: 0; 
        .icon { margin-left: 8px; vertical-align: middle; }
    }
    .watch-live { color: $color-live; }
    .watch-video {  }

    /* Dedicated LiveScores page */
    .widget-view-more-load { margin-bottom: 0; padding: 30px 0; }

    /* Results board for volleyball / basketball etc. */
    .results-board { display: flex; justify-content: center; margin-top: 20px; width: 100%; }
    .results-set { margin: 0 1px; }
    .results-current { color: $color-live; font-weight: bold; }
    .results-entry { background-color: $color-bg; border: 1px solid $color-gray-1; display: block; height: 30px; line-height: 30px; text-align: center; width: 30px; 
        &:first-child { border-bottom: none; }
    }
    .results-won { background-color: $color-bg-1; font-weight: bold; }
    .results-points { margin-left: 20px; }
    .results-points-player { background-color: $color-live; color: $color-text-inverted; }

    /* Formula 1 players */
    .f1-player-list { display: flex; justify-content: center; padding: 0 60px; width: 100%; }
    .f1-player { @include vertical-align; margin: 0 10px; }
    .f1-player-serial { font-weight: bold; margin-right: 5px; }
    .f1-player-face { border-radius: 15px; max-height: 30px; max-width: 30px; }
    .f1-upcoming-time { font-size: $font-heading-xs; font-weight: bold; margin-right: 10px; }
    .f1-upcoming-logo { margin-right: 10px; max-width: 30px; }

    /* As in Fixtures */
    .footer-generic { text-align: center;
        .btn-generic { margin: 50px 10px; }
    }
}
