.widget-footer-default-mobile { align-items: flex-end; font-size: $font-body-md; padding: 15px 10px;

    .footer-copyright { margin-bottom: 10px; width: 100%; }
    .footer-social {
        a { display: inline-block; margin-left: 10px; }
    }
    .footer-links {
        a { display: block; margin-left: 0;
            &:hover { color: $color-primary; }
        }
    }
}