.widget-league-standings { background-color: transparent; margin-bottom: 0; margin-top: 20px;

	.st-table { font-size: $font-body-xs; }
	/* Header */
	.st-table-head {
		.row > * { background-color: transparent; font-weight: normal; }
		.st-team { padding-left: 40px; }
	}
	/* Generic */
	.row { position: relative;
		& > * { padding: 5px; }
	}
	/* Table content */
	.st-table-body .row { background-color: $color-bg; margin-bottom: 1px;
		&:nth-child(2n+1) { background-color: $color-bg; }
		&:last-child { margin-bottom: 0; }
	}

	/* Fields */
	.st-color { height: 100%; left: 0; line-height: normal; padding: 0; position: absolute; top: 0; width: 5px; }
	.st-position { padding-right: 0; text-align: right; width: 25px; }
	.st-team { align-items: center; display: flex; font-weight: bold; }
	.st-team-logo { margin-right: 5px; }
	.st-team-name { line-height: 1.3em; }
	.st-field-small { width: 20px; }
	.st-goals { width: 36px; }
	.st-points { font-weight: bold; width: 30px; }

	/* Color legend */
	.st-color-legend { padding: 10px 20px 0; 

		.legend-row { margin-bottom: 5px; }
		.legend-color { height: 10px; width: 10px; }
	}
}