.widget-video-main { background-color: $color-bg; padding: 0 10px;

	/* Video player wrapper */
	.video-content { margin: 0 -10px; }
	.video-js { width: 100%; }
	/* Attempt to adjust YouTube proportions */
	.vjs-youtube { height: 0; max-width: 100%; overflow: hidden; padding-bottom: 56.25%; position: relative; }
	/* Play button */
	.vjs-big-play-centered .vjs-big-play-button { @include transition-fast; background: url(../image/icon/i_video_play.png) no-repeat center; background-size: contain; border: none; height: 60px; margin-left: -30px; margin-top: -30px; opacity: 0.8; width: 60px; 
		&:hover { opacity: 1; }
	}
	.video-js .vjs-big-play-button .vjs-icon-placeholder {
		&::before { content: normal; }
	}

	.video-title { line-height: 1.2em; padding-top: 10px; }
    .video-time { color: $color-text-gray-3; display: block; font-size: $font-body-sm; margin-bottom: 10px; margin-top: 5px; }
	
	/* General styles in layout/typography */
	.editor-content { font-size: $font-body-xm; line-height: 1.5em; margin: 10px 0 30px; }
	
	@include media-sm {
		.vjs-big-play-centered .vjs-big-play-button { height: 88px; margin-left: -44px; margin-top: -44px; width: 88px; }
	}
}
