.widget-view-more { @include transition-fast; @include vertical-align; display: block; font-size: $font-body-sm; font-weight: bold; margin-bottom: 20px; margin-top: 20px; padding-left: 10px; padding-right: 10px; text-align: right;

    .label { display: inline-block; position: relative; top: 2px; }
    .icon { @include transition-fast; background-image: url(../image/icon/ar_viewmore_normal.png); height: 20px; margin-left: 7px; vertical-align: middle; width: 20px; }

    &:hover { color: $color-primary;
        .icon { background-image: url(../image/icon/ar_viewmore_hover.png); }
    }

    /* Inside betting odds widget */
    .widget-odds & { color: $color-text-inverted; margin-bottom: 5px; margin-top: 0; padding-right: 5px;

        .icon { background-image: url(../image/icon/ar_viewmore_odds.png); }

        &:hover { color: $color-primary; 
            .icon { background-image: url(../image/icon/ar_viewmore_hover.png); }
        }
    }

    /* Inside news article, Livescore list, Team Overview, Match */
    .widget-news-article &, .widget-livescore-list &, .widget-team-match &, .widget-match-inline & { color: $color-text; }
}


.widget-view-more-load { display: block; line-height: 0; margin-bottom: 30px; padding-top: 10px; text-align: center;

    .icon { @include transition-fast; background: $color-bg url(../image/icon/ar_viewmore_big_normal.png) no-repeat center; border: 1px solid $color-gray-1; border-radius: 25px; display: inline-block; height: 50px; width: 50px; }
    .label { font-size: $font-body-md; font-weight: bold; line-height: 1.2em; margin-top: 7px; }

    &:hover {
        .icon { background-color: $color-primary; background-image: url(../image/icon/ar_viewmore_big_hover.png); border-color: transparent; }
    }

    /* After a box, not inside it */
    .box-generic-mobile + & { margin-top: 20px; }
}