.widget-odds { align-items: flex-end; display: flex; justify-content: center;
    
    &>* { position: relative; z-index: 1; }        
    
    .team-logo { height: 60px; margin: 0 15px; width: 60px; }
    
    .match-bet-wrap { align-items: flex-end; display: flex; flex: 1; justify-content: center; }
    .match-bet { border-bottom: 1px solid $color-gray-1; display: flex; padding: 0 10px;
    
        li { margin: 0 2.5px; text-align: center; 
            &:first-child .bet-odds { border-top-left-radius: 5px; }
            &:last-child .bet-odds { border-top-right-radius: 5px; }
        }
    }
    .bet-label { display: block; font-size: $font-body-xs; font-weight: bold; }
    .bet-odds { background-color: $color-bg; border: 1px solid $color-gray-1; border-bottom: none; display: inline-block; font-size: $font-body-md; font-weight: normal; padding: 4px 15px; }
    .odds-provider { line-height: 0; }
    
    .widget-view-more { margin-left: auto; width: 150px; /* Same as odds provider, so as to center bets */ }

    /* When there is only "1" and "2", as in LiveScores -> Volleyball */
    .bet-home + .bet-away { margin-left: 22px; }
}


/* Odds inside news photo */
.photo {
    .widget-odds { background: rgb(0,0,0); background: linear-gradient(0deg, rgba(0,0,0,1) 0%, rgba(0,0,0,0) 100%); bottom: 0; left: 0; padding-top: 50px; position: absolute; width: 100%; }
    .match-bet { border-bottom: none; padding: 0; 
        li { margin: 0 5px; }
    }
    .bet-label { color: $color-text-inverted; }
    .bet-odds { border-bottom: 1px solid $color-gray-1; padding: 5px 20px; }
}

/* Odds inside news article */
.widget-news-article { 
    .widget-odds { border-bottom: 1px solid $color-gray-1; margin-bottom: 15px; margin-top: 15px; }
    .match-bet { border-bottom: none; }
}

/* Odds inside prediction vote */
.widget-prediction-inline {
    .widget-odds { background-color: $color-bg-1; margin-left: -10px; margin-right: -10px; padding-top: 10px; }
}

/* Odds inside LiveScores list */
.widget-livescore-list {
    .widget-odds { border-bottom: 1px solid $color-gray-1; margin-bottom: -10px; margin-top: 5px; width: 100%; }
    .match-bet { border-bottom: none; 
        li { margin: 0 5px; }
    }
    .bet-label { font-size: $font-body-xs; }
    .bet-odds { padding: 4px 22px; }
}

/* Odds inside Team - Next Match, Match */
.widget-team-match, .widget-match-inline {
    .widget-odds { background-color: $color-bg-1; border-bottom: 1px solid $color-gray-1; padding: 10px 10px 0; }
    .match-bet { border-bottom: none; 
        li { margin: 0 5px; }
    }
    .bet-odds { padding: 4px 22px; }
}
.widget-match-inline { 
    .widget-odds { margin-left: -10px; margin-right: -10px; }
}